import {mapActions, mapState} from 'vuex';

import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import chatTyping from '@/components/chats/assets/typing.vue'
import typing from "@/components/chats/assets/typing";
import contacts from '@/components/contacts/index.vue'
import f from "@/application/functions";

export default {
  name: 'chatTopheader',
  props: {
    chat: Object,
    u: String,
    roomInfo: false,
    aboutUser: false
  },

  components: {
    chatName,
    chatIcon,
    chatTyping,
    contacts
  },

  data: function () {

    return {

      menuItems: [
        {
          click: "callupVideoHandler",
          title: "Video Call",
          icon: "fas fa-camera"
        },

        {
          click: "callupHandler",
          title: "Call",
          icon: "fas fa-phone"
        },
      ],

      menuItemsRoom: [
        {
          click: "AddMember",
          title: "Add",
          icon: "fas fa-user-plus"
        },

        {
          click: "MuteRoom",
          title: "Mute",
          icon: "fas fa-bell-slash"
        },
        {
          click: "LeaveFromRoom",
          title: "Leave and Delete",
          icon: "fas fa-sign-out-alt"
        },
      ],
      menuItemsRoomMuted: [
        {
          click: "AddMember",
          title: "Add",
          icon: "fas fa-user-plus"
        },

        {
          click: "MuteRoom",
          title: "UnMute",
          icon: "fas fa-bell"
        },
        {
          click: "LeaveFromRoom",
          title: "Leave and Delete",
          icon: "fas fa-sign-out-alt"
        },
      ],

      oneToOne: [
        {
          click: "MuteRoom",
          title: "Mute",
          icon: "fas fa-bell-slash"
        },
        {
          click: "LeaveFromRoom",
          title: "Leave and Delete",
          icon: "fas fa-sign-out-alt"
        },
      ],
      oneToOneMuted: [
        {
          click: "MuteRoom",
          title: "UnMute",
          icon: "fas fa-bell"
        },
        {
          click: "LeaveFromRoom",
          title: "Leave and Delete",
          icon: "fas fa-sign-out-alt"
        },
      ],

      loading: false,
      typing: false,
      userinfo: null,
      aboutUserShow: false,
      roomBanned: false,
      roomMuted: false
    }

  },


  watch: {
    //$route: 'getdata'
  },

  mounted: function () {
    this.getuserinfo()
  },


  computed: mapState({
    auth: state => state.auth,

    m_chat: function () {

      if (this.chat && this.chat.roomId) {
        let pushRules = this.core.mtrx.client._pushProcessor.getPushRuleById(this.chat.roomId)
        if (pushRules !== null) {
          this.roomMuted = true
          console.log("RoomMUTED", "TOPHEADER")
        }
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)
        console.log(m_chat, "M_CHAT")
        return m_chat || {}
      }

    },
    m_chat_typing: function () {

      if (!this.chat) return false

      var chat = this.chat.roomId
      var typing = this.$store.state.typing[chat] || {}

      var userTyping = _.find(typing, (t, i) => {

        if (t) {

          if (i == this.core.user.userinfo?.id) return false

          return true

        }

      })

      return userTyping || false
    },

  }),
  methods: {
    navigateToProfile(id) {
      this.$router.push({path: `/contact?id=${f.getmatrixid(id)}`})
    },
    getuserinfo: function () {
      if (this.u) {
        this.core.user.usersInfo(this.u).then(info => {
          this.userinfo = info[0]
        })
      }
    },
    addMember() {
      this.modalInviteUser()
    },
    menuItemClickHandler(item, rowObject) {
      if (item.click === 'AddMember') {
        this.$emit('addMember', true)
        this.$refs.dropdownMenu.hidePopup();
      }
      if (item.click === 'MuteRoom') {
        var roomId = this.chat.roomId
        var deviceID = this.core.mtrx.client.deviceId
        let self = this
        let pushRules = this.core.mtrx.client._pushProcessor.getPushRuleById(this.chat.roomId)
        if (pushRules !== null) {
          self.core.mtrx.client.deletePushRule('global', 'room', roomId)
          this.roomMuted = false

        } else {
          self.core.mtrx.client.setRoomMutePushRule('global', roomId, 'true')
          this.roomMuted = true
        }
        this.$refs.dropdownMenu.hidePopup();
      }
      if (item.click === 'LeaveFromRoom') {
        this.core.mtrx.client.leave(this.chat.roomId).then(r => {
          this.core.mtrx.client.forget(this.chat.roomId, true).then(r => {
            return r
          })
        })
        this.$router.push({path: '/chats'})

        this.$refs.dropdownMenu.hidePopup();
      }
      if (item.click === 'BanRoom') {

        let banUserId = ''
        let my = this.m_chat.myUserId
        let members = this.m_chat.currentState.getMembers()
        members.forEach(function (member) {
          if (member !== my) {
            banUserId = member.userId
          }
        })
        let roomID = this.chat.roomId
        this.core.mtrx.client.ban(roomID, banUserId, 'ban').then(r => {
          console.log(r, "BANNED")
        })
        this.roomBanned = true
        this.$refs.dropdownMenu.hidePopup();

      }
      if (item.click === 'Unban') {
        let banUserId = ''
        let my = this.m_chat.myUserId
        let members = this.m_chat.currentState.getMembers()
        members.forEach(function (member) {
          if (member !== my) {
            banUserId = member.userId
          }
        })
        this.core.mtrx.client.unban(this.chat.roomId, banUserId).then(r => {
          this.core.mtrx.client.invite(this.chat.roomId, banUserId)
        })
        console.log("UNBANNED")
        this.roomBanned = false
        this.$refs.dropdownMenu.hidePopup();
      }

    },
    modalInviteUser() {
      this.inviteUserOpened = true
    },

    closeModal() {
      this.inviteUserOpened = false
    },

    closeContactModal(value) {
      this.inviteUserOpened = value
    },
    callupHandler(rowObject) {

    },

    callupVideoHandler(rowObject) {

    },
  }

}